@media (min-width: 768px){
    body.sidenav-toggled .app-sidebar {
        left: 0;
        width: 80px;
        overflow: hidden;
    }
    .sidenav-toggled .app-sidebar__user-name, 
    .sidenav-toggled .app-sidebar__user-designation, 
    .sidenav-toggled .angle, 
    .sidenav-toggled .app-sidebar__user-name, 
    .sidenav-toggled .user-notification, 
    .sidenav-toggled .app-sidebar__user-name, 
    .sidenav-toggled .user-info{
        display: none;
    }
    .sidenav-toggled .app-sidebar__user {
        padding: 12px 0px 12px 0;
        margin-bottom: 0px;
        border-bottom: 0;
    }
    .sidenav-toggled .side-badge {
        display: none !important;
    }
    .sidenav-toggled .avatar-xl {
        width: 55px !important;
        height: 55px !important;
    }
    .sidenav-toggled .profile-status {
        right: 22px;
        width: 11px;
        height: 11px;
        top: 57px;
    }
    .sidenav-toggled .side-menu {
        margin-top: 0px;
    }
    .sidenav-toggled .side-menu .slide .side-menu__item {
        display: block;
        padding: 8px 0 9px 0;
        margin: 0 auto;
        text-align: center;
        border-left: 0;
    }
    .sidenav-toggled .side-menu .slide .side-menu__item .side-menu__icon{
        margin: 0px;
    }
    .sidenav-toggled .side-menu .slide .side-menu__item.active:before{
        display: none;
    }
    .sidenav-toggled .main-sidebar-header {
        width: 80px;
    }
    .sidenav-toggled .main-sidebar-header .min-menu {
        display: block;
        font-size: 30px;
        font-weight: 600;
        color: #2c5091;
        line-height: 60px;
        text-decoration: none;
    }
    .sidenav-toggled .main-sidebar-header .min-menu span{
        color: #f27e21;
    }
    .sidenav-toggled .main-sidebar-header .logo-light {
        display: none;
    }
    .sidenav-toggled .side-menu__label {
        display: none;
        position: relative;
        padding: 0;
        margin: 0;
        left: 0;
        color: #fff;
        opacity: 1;
        background: transparent;
        font-size: 12px;
        box-shadow: none;
    }
    .sidenav-toggled .slide .side-menu__label {
        border-bottom-right-radius: 0;
    }
    .sidenav-toggled .app-content {
        margin-left: 80px;
    }
    .sidenav-toggled .main-header {
        padding-left: 80px;
    }
    .sidenav-toggled .close-toggle {
        display: block !important;
        color: rgb(0, 0, 0);
        font-size: 18px;
        line-height: 42px;
        text-align: center;
    }
    /** min-manu hover **/
    .sidenav-toggled-open .app-sidebar {
        left: 0px;
        width: 240px !important; 
    }
    .sidenav-toggled-open .app-sidebar__user .user-info {
        text-align: center;
        display: block !important;
        margin: 0px auto;
    }
    .sidenav-toggled-open .avatar-xl {
        width: 72px !important;
        height: 72px !important;
        font-size: 36px !important;
    }
    .sidenav-toggled-open .profile-status {
        right: 97px;
        width: 11px;
        height: 11px;
        top: 70px;
    }
    .sidenav-toggled-open .app-sidebar .slide .side-menu__item{
        display: flex;
    }
    .sidenav-toggled-open .app-sidebar .slide .side-menu__item.active::before {
        content: "";
        width: 3px;
        height: 100%;
        position: absolute;
        left: 0px;
        display: block;
        background: #fff;
    }
    .sidenav-toggled-open .side-menu__label {
        white-space: nowrap;
        -webkit-box-flex: 1;
        opacity: 1;
        position: initial;
        font-size: 0.875rem;
        line-height: 1;
        vertical-align: middle;
        font-weight: 400;
        display: block !important;
        flex: 1 1 auto;
    }
    .sidenav-toggled-open .side-menu .slide .side-menu__item {
        text-align: left;
        padding: 10px;
        margin: 0px;
        border-radius: 0px;
    }
    .sidenav-toggled-open .side-menu .side-menu__icon {
        margin-right: 12px !important;
    }
    .sidenav-toggled-open .main-sidebar-header .min-menu {
        display: none
    }
    .sidenav-toggled-open .main-sidebar-header .logo-light {
        display: block;
    }
    .sidenav-toggled-open .main-sidebar-header{
        width: 240px;
    }
}

.app-sidebar__toggle .close-toggle, .sidenav-toggled .open-toggle {
    display: none;
    text-decoration: none;
}
.app-sidebar__toggle .open-toggle{
    text-decoration: none;
}
.app-sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    color: #14112d;
    width: 240px;
    max-height: 100%;
    z-index: 1000;
    background:rgba(255,255,255,.7);
    -webkit-box-shadow: -8px 12px 18px 0 #b3bcd0;
    box-shadow: -8px 12px 18px 0 #b3bcd0;
    border-right: 1px solid #dee4ec;
    padding-bottom: 0;
    -webkit-transition: width 0.3s cubic-bezier(0, 0, 0.2, 1);
    transition: width 0.3s cubic-bezier(0, 0, 0.2, 1);
    transform: translate3d(0, 0, 0);
    background-image: url(../images/menu-bg.png);
    background-size: cover;
}
.main-sidebar-header {
    flex-shrink: 0;
    height: 64px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    width: 240px;
    background: rgba(255,255,255,1);
    border-bottom: 1px solid #eae8f1;
    position: fixed;
    /*border-right: 1px solid #eae8f1;*/
    white-space: nowrap;
    visibility: visible;
    -webkit-transition: width 0.3s cubic-bezier(0, 0, 0.2, 1);
    transition: width 0.3s cubic-bezier(0, 0, 0.2, 1);
    transform: translate3d(0, 0, 0);
}
.desktop-logo {
    text-align: center;
    margin: 0 auto;
    justify-content: center;
}
.main-logo {
    display: flex;
    align-items: center;
    position: relative;
    margin: 0 auto;
    justify-content: center;
    text-align: center;
    width: 180px;
}
@media (max-width: 768px){
.desktop-logo {
    display: none;
}
}
@media (min-width: 768px){
    .mobile-logo {
        display: none;
    }
    .min-menu{
        display: none;
    }
}
.main-sidemenu {
    margin-top: 64px;
    height: calc(100% - 64px);
    background: rgba(58, 58, 125, 0.5);
}
.app-sidebar__user {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #a8a8a8;
    width: 100%;
    display: inline-block;
    background-size: cover;
    background-position: left;
}
.app-sidebar__user .user-pro-body {
    display: block;
    padding: 15px 0;
}
.app-sidebar__user .user-info {
    margin: 0 auto;
    text-align: center;
}
.avatar-xl {
    width: 72px !important;
    height: 72px !important;
    font-size: 36px !important;
}
.avatar {
    position: relative;
    width: 36px;
    height: 36px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-weight: 600;
    font-size: 16px;
    background-color: #0162e8;
}
.brround {
    border-radius: 50%;
}
.app-sidebar__user .user-pro-body img {
    display: block;
    margin: 0 auto 0px;
    border: 2px solid #c9d2e8;
    box-shadow: 0px 5px 5px 0px rgba(44, 44, 44, 0.2);
    padding: 3px;
    background: #fff;
}
.profile-status {
    content: '';
    position: absolute;
    bottom: 0;
    right: 103px;
    width: 12px;
    height: 12px;
    background-color: #22c03c;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.95);
    border-radius: 100%;
    top: 73px;
    animation: pulse 2s infinite;
    animation-duration: .9s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out;
    border: 2px solid #fff;
}
.app-sidebar__user .user-info h4 {
    color: #14112d;
    font-size: 15px;
}
.user-info .text-muted {
    color: #f2f5fb !important;
    font-size: 13px;
}
.side-menu {
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    padding-top: 25px;
}
.side-menu .side-menu__icon {
    font-size: 23px;
    margin-right: 14px;
    width: 22px;
    height: 22px;
    border-radius: 3px;
    text-align: center;
    color:#fff;
    fill:#fff;
}
.slide {
    margin: 0 0 8px 0;
    padding:0px 20px;
}
.slide a{
    text-decoration: none;
}
.slide.is-expanded a {
    color: #6d7790;
    text-decoration: none;
}
.side-menu__item {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 0;
    font-size: 14px;
    font-weight: 500;
    -webkit-transition: border-left-color 0.3s ease, background-color 0.3s ease;
    -o-transition: border-left-color 0.3s ease, background-color 0.3s ease;
    transition: border-left-color 0.3s ease, background-color 0.3s ease;
    /*height: 34px;*/
}
.side-menu .slide .side-menu__item {
    padding: 10px;
    background: rgba(44, 80, 145, 0.5);
    margin: 0;
    color: #fff;
    border-radius: 2px;

}
.side-menu__label {
    white-space: nowrap;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    white-space: nowrap;
    color: #fff;
    position: relative;
    font-size: 13.5px;
    line-height: 1;
    vertical-align: middle;
    font-weight: 400;
}
.slide:hover .side-menu__label, .slide:hover .angle, .slide:hover .side-menu__icon {
    /*color: #fff !important;
    fill: #fff !important;*/
}
.side-menu .slide .side-menu__item:hover {
    /*box-shadow: 2px 2px 1px #fff;*/
    background: rgba(44, 80, 145, 0.7)
}
.side-menu__item.active:hover,
.side-menu__item.active{
    background:rgba(44, 80, 145, 1) !important;
    /*box-shadow: 2px 2px 1px #a54900 !important;*/
}
.side-menu__item.active .side-menu__label ,
.side-menu__item.active .side-menu__icon{
    color: #fff;
}
.slide.is-expanded .side-menu__label, .slide.is-expanded .side-menu__icon, .slide.is-expanded .angle {
    color: #0162e8 !important;
}
.app-sidebar .slide .side-menu__item.active::before {
    content: '';
    width: 3px;
    height: 100%;
    background: rgba(255,255,255,.8);
    position: absolute;
    left: 0;
}
.app-sidebar__user .user-info .text-muted {
    color: #8991a5 !important;
    font-size: 13px;
}
.app-sidebar .mCSB_draggerContainer {
    right: -11px !important;
}
 .app-sidebar .mCSB_scrollTools .mCSB_draggerRail {
    background: rgba(255, 255, 255, 0.2);
}
.app-sidebar .mCS-minimal.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
    background: rgba(0, 0, 0, 0.05);
}
.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
    width: 2px;
}

@-webkit-keyframes pulse {
    0% {
        -webkit-box-shadow: 0 0 0 0 rgba(34, 192, 60, 0.9); 
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(34, 192, 60, 0); 
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(34, 192, 60, 0); 
    }
}

@keyframes pulse {
    0% {
        -moz-box-shadow: 0 0 0 0 rgba(34, 192, 60, 0.9);
        box-shadow: 0 0 0 0 rgba(34, 192, 60, 0.7); 
    }
    70% {
        -moz-box-shadow: 0 0 0 10px rgba(34, 192, 60, 0);
        box-shadow: 0 0 0 10px rgba(34, 192, 60, 0); 
    }
    100% {
        -moz-box-shadow: 0 0 0 0 rgba(34, 192, 60, 0);
        box-shadow: 0 0 0 0 rgba(34, 192, 60, 0); 
    }
}