.input-checkbox {
    display: inline-block;
    position: relative;
}
.input-checkbox input {
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    width: 17px;
    height: 17px;
    opacity: 0;
    border-radius: 2px;
    cursor: pointer;
    z-index: 1;
}
.input-checkbox span {
    width: 17px;
    height: 17px;
    border: 2px solid #126086;
    display: block;
    border-radius: 2px;
}
.input-checkbox span:after {
	content: '\eed6';
    font-family: IcoFont;
    font-style: normal;
    font-size: 19px;
    position: absolute;
    top: 0px;
    left: -1px;
    line-height: 17px;
    width: 20px;
    color: #fff;
    transform: scale(0);
    transition: all .3s ease-in-out;
}
.input-checkbox input:checked + span{
	background: #126086;
}
.input-checkbox input:checked + span:after {
	transform: scale(1);
}
