.date-filter form .btn{
    font-size: 12px;
    width: 80px;
}
@media (min-width: 768px){
	.app-content {
	    margin-left: 250px;
	}
	.main-header {
	    z-index: 975 !important;
	    width: 100% !important;
	    left: 0;
	    padding-left: 240px;
	    position: fixed;
	}
	.app.sidenav-toggled .main-header {
	    padding-left: 80px;
	}
	.app.sidenav-toggled .app-content {
	    margin-left: 80px;
	}
	.app.sidenav-toggled .app-sidebar {
	    left: 0;
	    width: 80px;
	    overflow: hidden;
	}
	.responsive-logo {
	    display: none;
	}
}
@media (min-width: 992px){
	.main-content {
	    padding-top: 0 !important;
	    margin-left: 240px;
	}
	.sticky-pin {
	    position: fixed;
	    top: 0;
	    width: 100%;
	    z-index: 1;
	}
	.main-header > .container, .main-header > .container-fluid {
	    padding-left: 15px;
	    padding-right: 15px;
	}
	.main-header-center {
	    display: block;
	}
	.main-header-left .form-control {
	    height: 40px;
	    border-radius: 20px;
	    background: #ecf0fa;
	    border: 1px solid #ecf0fa;
	}
	.main-header-left .btn {
	    position: absolute;
	    top: 0;
	    right: 0;
	    background-color: transparent;
	    height: 40px;
	    color: #b4bdce;
	    transition: none;
	    font-size: 16px;
	    padding-right: 13px;
	}
}
@media (min-width: 1135px){
	.main-header-left .form-control {
	    width: 350px;
	}
}
@media (max-width:767px){
    .sidenav-toggled .app-sidebar__overlay {
        visibility: visible;
    }
    .responsive-logo{
        max-width: 130px;
    }
    .responsive-logo img{
        width: calc(100% - 20px);
        margin-left: 10px;
    }
    .app-sidebar {
        left: -240px;
    }
    .app-sidebar {
        z-index: 1029 !important;
        top: 0 !important;
    }
    .app-sidebar__toggle {
        order: -1;
    }
    .main-header-center{
        display: none !important;
    }
    .sidenav-toggled .app-sidebar {
        left: 0;
    }
    .desktop-logo.logo-light{
        display: block;
    }
    .app-sidebar__overlay {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 9;
    }
    .main-header-message .dropdown-menu, 
    .main-header-notification .dropdown-menu{
        right: -50px !important;
        width: 280px !important;
    }
    .main-header-message .dropdown-menu:after, 
    .main-header-notification .dropdown-menu:after{
        right: 63px;
    }
}




