	overflow: hidden;
}
.inner-section *{
	position: relative;
	z-index: 1
}
.inner-section:after{
	content: '';
	width: 200px;
	height: 200px;
	border-radius: 100%;
	background: rgba(18, 96, 134, 0.16);
	position: absolute;
	left: -30px;
	bottom: -90px;
}
.register-section:after{
	right: -30px;
	left: auto;
}
.register-section{
	border-radius: 0px 6px 6px 0px
}
.inner-section:before{
	/*content: '';*/
	width: 100px;
	height: 100px;
	/*background: rgba(18, 96, 134, 0.4);*/
	background:rgba(131, 193, 50,.6);
	position: absolute;
	-webkit-animation: animationFramesOne 10s infinite linear;
	        animation: animationFramesOne 10s infinite linear;
}
.inner-section p{
	margin-bottom: 0px;
	font-size: 16px;
	font-family: 'Roboto', sans-serif;
	font-family: 'Ubuntu', sans-serif;
	position: relative;
	z-index: 1;
	color: #282871;
}
.inner-section p a{
	font-weight: 600;
	color: #f27e21;
}
.inner-section h2{
	font-size: 40px;
	font-weight: 600;
	color: #000000;
	font-family: 'Ubuntu', sans-serif;
	/*text-transform: capitalize;*/
}
.inner-section h4{
	font-size: 20px;
	font-weight: 400;
	font-family: 'Ubuntu', sans-serif;
	color: #7a7a7a;
}


.message-conntainer{
	padding: 60px 0;
}

@-webkit-keyframes animationFramesOne {
	 0% {
    transform: translate(0px, 0px) rotate(0deg);
  }
  20% {
    transform: translate(73px, -1px) rotate(36deg);
  }
  40% {
    transform: translate(141px, 72px) rotate(72deg);
  }
  60% {
    transform: translate(83px, 122px) rotate(108deg);
  }
  80% {
    transform: translate(-40px, 72px) rotate(144deg);
  }
  100% {
    transform: translate(0px, 0px) rotate(0deg);
  }
}
@keyframes animationFramesOne {
  0% {
    transform: translate(0px, 0px) rotate(0deg);
  }
  20% {
    transform: translate(73px, -1px) rotate(36deg);
  }
  40% {
    transform: translate(141px, 72px) rotate(72deg);
  }
  60% {
    transform: translate(83px, 122px) rotate(108deg);
  }
  80% {
    transform: translate(-40px, 72px) rotate(144deg);
  }
  100% {
    transform: translate(0px, 0px) rotate(0deg);
  }
}










