.checkbox-custom{
	position: relative;
}
.checkbox-custom input{
	display: none;
}
.checkbox-custom label{
	font-weight: 400 !important;
	position: relative;
	padding-left: 30px;
	cursor: pointer;
}
.checkbox-custom label:after{
	content: '';
	position: absolute;
	width: 20px;
	height: 20px;
	border:2px solid #fff;
	left: 0px;
	top:0px;
}
.checkbox-custom label:before{
	content: '\eed6';
	font-family: IcoFont;
	font-style: normal;
	font-size: 21px;
	position: absolute;
	top: 0px;
	left: 0px;
	line-height: 20px;
	width: 20px;
	color: #126086;
	transform: scale(0);
	transition: all .3s ease-in-out;
	z-index: 1;
}
.checkbox-custom input:checked + label:after{
	border-color: #ffffff;
	background: #ffffff;
	border-radius: 3px;
}
.checkbox-custom input:checked + label:before{
	transform: scale(1);
}
